<template>
  <div class="dishes-list">
    <div class="dishes-search"></div>
    <div class="dishes-items" v-for="(item, index) in dishesList" :key="index">
      <router-link :to="`/dishes/${item.doc._id}`" class="d-block card dishes-item text-decoration-none mb-2">
        <div class="card-body d-flex">
          <div class="dishes-item__avatar mr-2">
            <img class="mx-auto" v-if="item.doc.image" :src="item.doc.image"/>
          </div>
          <div class="dishes-item__content w-100 d-flex flex-column">
            <h6>{{item.doc.title}}</h6>
            <div class="mt-auto">
              <p class="text-center text-primary m-0">На <strong>{{ item.doc.dishes_portion }}</strong> гр. продукту</p>
              <div class="item-information d-flex text-center">
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">БЖ</span>
                  <span class="item-information__footer">{{ toFix((bxbjCalc(bxCalc(item.doc.b_var, item.doc.dishes_portion), bjCalc(item.doc.j_var, item.doc.dishes_portion)))/item.doc.itemIds.length)}}</span>
                </div>
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">У</span>
                  <span class="item-information__footer">{{ toFix((byCalc(item.doc.y_var, item.doc.dishes_portion))/item.doc.itemIds.length)}}</span>
                </div>
                <div class="item-information__item flex-fill d-flex flex-column">
                  <span class="item-information__header">ХЕ</span>
                  <span class="item-information__footer">{{ toFix((allCalc(bxCalc(item.doc.b_var, item.doc.dishes_portion),bjCalc(item.doc.j_var, item.doc.dishes_portion),byCalc(item.doc.y_var, item.doc.dishes_portion)))/item.doc.itemIds.length)}}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DishesList',
  data() {
    return {
      dishesList: [],
      length: 1,
    }
  },
  computed: {
    bx_param: {
      get() {
        return this.$store.state.option.bx_param
      }
    },
    bj_param: {
      get() {
        return this.$store.state.option.bj_param
      }
    }
  },
  methods: {
    calculate(value, gram) {
      return (parseFloat(value) * gram) / 100
    },
    bxCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bx_param)) / 12).toFixed(1);
    },
    bjCalc(value, gram) {
      return ((this.calculate(value, gram) * parseFloat(this.bj_param)) / 12).toFixed(1);
    },
    byCalc(value, gram) {
      return (this.calculate(value, gram) / 12).toFixed(1);
    },
    bxbjCalc(p1, p2) {
      return (parseFloat(p1) + parseFloat(p2)).toFixed(1);
    },
    allCalc(p1, p2, p3) {
      return (parseFloat(p1) + parseFloat(p2) + parseFloat(p3)).toFixed(1);
    },
    toFix(val){
      return val.toFixed(1)
    },
    async getCategories() {
      try {
        await this.$pouch.allDocs({
          include_docs: true,
          attachments: true
        }, 'dishes').then((res) => {
          this.dishesList = res.rows
        });
      } catch (error) {
        console.error(error)
      }
    }
  },
  mounted() {
    this.getCategories();
  }
}
</script>

<style scoped lang="scss">
.card-body {
  padding: 5px;
}
.dishes-item {
  color: #000000;
  &__avatar {
    width: 100px;
    min-width: 100px;
  }
}
.item-information {
  &__item {
    margin: 0 2px;
    .item-information__header {
      border-bottom: 1px solid green;

    }
    .item-information__footer {
      font-weight: 500;
      font-size: 18px;
    }
  }
}
</style>
